module.exports = "import { Component } from '@angular/core';\r\n\r\n@Component({\r\n  selector: 'nz-demo-modal-async',\r\n  template: `\r\n    <button nz-button nzType=\"primary\" (click)=\"showModal()\">\r\n      <span>Show Modal</span>\r\n    </button>\r\n    <nz-modal [(nzVisible)]=\"isVisible\" nzTitle=\"Modal Title\" (nzOnCancel)=\"handleCancel()\" (nzOnOk)=\"handleOk()\" [nzOkLoading]=\"isOkLoading\">\r\n      <p>Modal Content</p>\r\n    </nz-modal>\r\n  `,\r\n  styles: []\r\n})\r\nexport class NzDemoModalAsyncComponent {\r\n  isVisible = false;\r\n  isOkLoading = false;\r\n\r\n  showModal(): void {\r\n    this.isVisible = true;\r\n  }\r\n\r\n  handleOk(): void {\r\n    this.isOkLoading = true;\r\n    window.setTimeout(() => {\r\n      this.isVisible = false;\r\n      this.isOkLoading = false;\r\n    }, 3000);\r\n  }\r\n\r\n  handleCancel(): void {\r\n    this.isVisible = false;\r\n  }\r\n}\r\n"