module.exports = "import { Component } from '@angular/core';\r\n\r\n\r\n@Component({\r\n  selector: 'nz-demo-modal-footer',\r\n  template: `\r\n    <button nz-button nzType=\"primary\" (click)=\"showModal()\">\r\n      <span>Show Modal</span>\r\n    </button>\r\n    <nz-modal [(nzVisible)]=\"isVisible\" [nzTitle]=\"modalTitle\" [nzContent]=\"modalContent\" [nzFooter]=\"modalFooter\" (nzOnCancel)=\"handleCancel()\">\r\n      <ng-template #modalTitle>\r\n        Custom Modal Title\r\n      </ng-template>\r\n\r\n      <ng-template #modalContent>\r\n        <p>Modal Content</p>\r\n        <p>Modal Content</p>\r\n        <p>Modal Content</p>\r\n        <p>Modal Content</p>\r\n        <p>Modal Content</p>\r\n      </ng-template>\r\n\r\n      <ng-template #modalFooter>\r\n        <span>Modal Footer: </span>\r\n        <button nz-button nzType=\"default\" (click)=\"handleCancel()\">Custom Callback</button>\r\n        <button nz-button nzType=\"primary\" (click)=\"handleOk()\" [nzLoading]=\"isConfirmLoading\">Custom Submit</button>\r\n      </ng-template>\r\n    </nz-modal>\r\n  `,\r\n  styles: []\r\n})\r\nexport class NzDemoModalFooterComponent {\r\n  isVisible = false;\r\n  isConfirmLoading = false;\r\n\r\n  constructor() { }\r\n\r\n  showModal(): void {\r\n    this.isVisible = true;\r\n  }\r\n\r\n  handleOk(): void {\r\n    this.isConfirmLoading = true;\r\n    setTimeout(() => {\r\n      this.isVisible = false;\r\n      this.isConfirmLoading = false;\r\n    }, 3000);\r\n  }\r\n\r\n  handleCancel(): void {\r\n    this.isVisible = false;\r\n  }\r\n}\r\n"