module.exports = "import { Component } from '@angular/core';\r\nimport { NzModalService } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-modal-locale',\r\n  template: `\r\n    <div>\r\n      <button nz-button nzType=\"primary\" (click)=\"showModal()\">Modal</button>\r\n      <nz-modal\r\n        [(nzVisible)]=\"isVisible\"\r\n        nzTitle=\"Modal\"\r\n        nzOkText=\"Ok\"\r\n        nzCancelText=\"Cancel\"\r\n        (nzOnOk)=\"handleOk()\"\r\n        (nzOnCancel)=\"handleCancel()\"\r\n      >\r\n        <p>Bla bla ...</p>\r\n        <p>Bla bla ...</p>\r\n        <p>Bla bla ...</p>\r\n      </nz-modal>\r\n    </div>\r\n    <br/>\r\n    <button nz-button nzType=\"info\" (click)=\"showConfirm()\">Confirm</button>\r\n  `,\r\n  styles: []\r\n})\r\nexport class NzDemoModalLocaleComponent {\r\n  isVisible = false;\r\n\r\n  constructor(private modalService: NzModalService) { }\r\n\r\n  showModal(): void {\r\n    this.isVisible = true;\r\n  }\r\n\r\n  handleOk(): void {\r\n    this.isVisible = false;\r\n  }\r\n\r\n  handleCancel(): void {\r\n    this.isVisible = false;\r\n  }\r\n\r\n  showConfirm(): void {\r\n    this.modalService.confirm({\r\n      nzTitle: 'Confirm',\r\n      nzContent: 'Bla bla ...',\r\n      nzOkText: 'OK',\r\n      nzCancelText: 'Cancel'\r\n    });\r\n  }\r\n}\r\n"